/* -----------------------------------------------
* home Module
* homeページ用
-------------------------------------------------- */
@use '../mixin/mixin' as *;
@use '../setting/setting' as *;


// mv
.mv {
  margin-top: 99px;
  @include sp(){
    margin-top: 47px;
  }
  img {
    width: 100%;
  }
}

//sale
.sale {
  margin: 120px 0;
  @include sp{
    margin: 40px 0;
  }
  &__img {
    text-align: center;
  }
}
.sale-coupon{
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  bottom:  40px;
  @include sp{
    bottom: vw(24,375);
  }
  &.ptn01 {
    position: relative;
    display: block;
  }
}


//worries
.worries {
  position: relative;
  &::after {
    display: block;
    content: "";
    width: 100%;
    height: 150px;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    margin: auto;
    margin-top: 24px;
    background: url(../img/sankaku.svg) no-repeat;
    background-position: center center;
    z-index: 2;
    @include sp(){
      width: 245px;
      height: 78px;
      background-size: contain;
    }
  }
  &__txt {
    margin-top: 40px;
    text-align: center;
  }
}


//doble
.doble{
  position: relative;
  &__box {
    padding: 80px 0;
    background: $main_color02;
    @include sp{
      padding: 80px 0 40px;
    }
  }
  &-flex {
    display: flex;
    justify-content: center;
    column-gap: 40px;
  }
  &__box01 {
    width: calc((100% - 40px) / 2);
    @include sp(){
      width: 100%;
    }
  }
  &__ttl {
    font-size: min(1.66vw,28px);
    color: #fff;
    font-weight: 500;
    @include sp(){
      font-size: min(14px,#{vw(14,375)});
      text-align: center;
    }
    .big {
      font-size: min(5.2vw,88px);
      font-weight: 700;
      @include sp(){
        font-size: min(46px,#{vw(46,375)});
      }
    }
  }
  &__img {
    @include sp() {
      margin-top: 3.7vw;
    }
    img {
      border-radius: 8px;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
.doble-list {
  margin-top: 48px;
  @include sp(){
    margin-top: 24px;
  }
  &__item {
    margin-top: 24px;
    text-align: center;
    position: relative;
    @include sp{
      margin-top: 12px;
    }
    &:first-child {
      margin-top: 0;
    }
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background: $main_color01;
      position: absolute;
      top: 4px;
      left: 4px;
      z-index: 0;
    }
  }
  &__txt {
    background: #fff;
    padding: 14px 20px;
    z-index: 2;
    position: relative;
    font-size: 27px;
    font-weight: 700;
    color: $main_color01;
    @include sp{
      font-size: 24px;
    }
  }
}

//warning
// warning
.warning {
  &-box {
    padding: 41px 190px 80px;
    text-align: center;
    // background: repeating-linear-gradient(-45deg, #E6CA67, #E6CA67 15px, #F7D96A 15px, #F7D96A 60px);
    background-color: #444;
    @include sp(){
      // margin: 0 -20px;
      padding: 40px 20px;
    }
  }
  &__ttl {
    display: inline-block;
    margin-bottom: 38px;
    font-size: 40px;
    font-family: "ShipporiMincho";
    font-weight: bold;
    position: relative;
    color: #fff;
    @include sp(){
      font-size: 20px;
    }
    &::after {
      display: inline-block;
      content: "";
      width: 100%;
      height: 2px;
      background: #E6CA67;
      position: absolute;
      bottom: 0;
      left: 0;
   
    }
  }
  &-list {
    background: #777;
    padding: 90px 40px;
    border-radius: 8px;
    @include sp(){
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 24px 19px;
    }
  }
  li {
    margin-top: 40px;
    @include sp(){
      margin-top: 15px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  &__con01 {
    display: flex;
    align-items: center;
    column-gap: 24px;
    position: relative;
    padding-right: 45px;
    cursor: pointer;
    @include sp(){
      align-items: flex-start;
      column-gap: 4px;
      padding-right: 20px;
    }
    img {
      width: 40px;
    }
    >span.toggle-btn{
      display: block;
      width: 25px;
      height: 25px;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      margin: auto 0;
      @include sp{
        width: 20px;
        height: 20px;
      }
      &::before,
      &::after{
        content: "";
        display: block;
        width: 25px;
        height: 4px;

        background-color: #fff;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        @include sp{
          width: 20px;
        }
      }
      &::before{
        transform: rotate(90deg);
        transition: $transition01;
      }
    }
    >span.toggle-btn.is-active{
      &::before{
        transform: rotate(0);
      }
    }
  }
  &__con02{
    text-align: left;
    margin-top: 10px;
    padding: 0 45px 0 64px;
    font-weight: 500;
    font-size: 21px;
    display: none;
    color: #fff;
    @include sp{
    padding: 0 45px 0 44px;
    font-size: 16px;
    }
  }
  &__txt {
    font-size: 28px;
    font-family: "ShipporiMincho";
    font-weight: bold;
    text-align: left;
    color: #fff;
    @include sp(){
      font-size: 18px;
    }
  }
}
.warning-list-con{
  @include sp{
    width: 100%;
  }
}

//satisfaction
.satisfaction {
  &__box {
    background: linear-gradient(180deg, #fff 0 70%,$main_color01 70% 100%);
    padding-bottom: 78px;
    @include sp(){
      padding-bottom: 40px;
      background: linear-gradient(180deg, #fff 0 77%,$main_color01 77% 100%);
    }
  }
  &__ttl {
    .black {
      color: #000;
    }
    .c-ttl{
      @include sp(){
        font-size: 24px;
      }
    }
    .deco {
      font-size: 32px;
      color: $main_color02;
    }
  }
  &__txt{
    color: #fff;
    font-size: 12px;
    margin-top: 10px;
    padding-left: 8px;
  }
  &__con{
    @include sp(){
      margin-top: 30px;
    }
  }
}
.satisfaction-list {
  display: flex;
  column-gap: 15px;
  justify-content: center;
  @include sp(){
    flex-wrap:wrap;
    //flex-direction: column;
  }
  &__item {
    @include pc(){
      width: calc((100% - 30px) / 3);
    }
    @include sp(){
      width: calc((100% - 15px) / 2);
    }
    &:nth-child(1){
      @include sp(){
        width: 100%;
        padding: 0 25%;
      }
    }
    &:nth-child(2){

    }
    &:nth-child(3){

    }
  }
  &__box {
    padding-top: 24px;
    padding-bottom: 34px;
    background: #fff;
    border: solid 3px $main_color02;
    border-radius: 4px;
    @include sp(){
      padding-top: 12px;
      padding-bottom: 18px;
    }
  }
  &__dec {
    position: relative;
    text-align: center;
    padding-left: 34px;
    background: $main_color02;
    border-radius: 0 4px 4px 0;
    color: #fff;
    font-size: 24px;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    left: -8px;
    @include pc(){
      width: 271px;
      height: 51px;
    }
    @include sp(){
      font-size: 14px;
      padding-left: 10%;
      padding-top: 6px;
      padding-bottom: 6px;
    }

    &::after {
      content: "";
      display: block;
      position: absolute;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 8px 4px 0;
      border-color: transparent $main_color01 transparent transparent;
      top: 100%;
      left: 0;
    }
  }
  &__txt {
    text-align: center;
    transform: translateY(-51px);
  }
  &__img {
    margin-left: auto;
    margin-right: auto;
    padding: 0 24px;
    margin-top: 30px;
    width: 240px;
    @include sp(){
      width: 100%;
      padding: 0 10px;
      margin-top: 10px;
    }
    img {
      width: 100%;
    }
  }
  &__cap01{
    font-size: 12px;
    color: $main_color01;
    margin-bottom: 24px;
  }
}


// contact
.contact-box {
  padding: 40px;
  background: $main_color02;
  @include sp(){
    padding: 40px 0;
  }
  &__inner {
    padding: 40px 54px;
    background: #fff;
    border-radius: 24px;
    @include sp(){
      padding: 24px 14px;
    }
  }
}
.contact-box01 {
  display: flex;
  column-gap: 40px;
  justify-content: center;
  @include sp(){
    display: block;
  }
  &-item {
    width: calc((100% - 40px) / 2);
    @include sp(){
      margin-top: 20px;
      width: 100%;
      &:nth-child(1) {
        margin-top: 0;
      }
    }
  }
  &-item01 {
    padding: 76px;
    background: #04C755;
    border-radius: 24px;
    text-align: center;
    @include sp(){
      padding: 16px 20px;
    }
    &__txt {
      color: #fff;
      font-size: 20px;
      font-weight: bold;
    }
    &__img {
      margin-top: 16px;
      img {
        width: 100%;
      }
    }
    &__sub {
      margin-top: 8px;
      padding: 8px 20px;
      text-align: center;
      background: #fff;
    }
    &__cont {
      @include pc(){
        .c-contact-01__ttl02 {
          margin-top: 15px;
        }
        .c-line-btn-wrap {
          margin-top: 15px;
        }
        .contact-box01-item01__sub {
          margin-top: 15px;
        }
      }
    }
  }
  &-item02 {
    padding: 85px 30px;
    background: $main_color01;
    border-radius: 24px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    @include sp(){
      padding: 28px 8px;
    }
    &__txt,
    &__nam,
    &__time {
      color: #fff;
    }
    &__txt {
      text-align: center;
    }
    &__nam {
      padding-left: 50px;
      font-size: 40px;
      font-family: 'ShipporiMincho';
      position: relative;
      @include sp(){
        padding-left: 20px;
        font-size: 16px;
      }
      &::before,
      &::after {
        display: block;
        content: "";
        position: absolute;
      } 
      &::after {
        width: 48px;
        height: 48px;
        background: url(../img/tel-icon.svg);
        background-size: cover;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        margin: auto;
        transform: translateX(-132px);
        @include sp(){
          width: 24px;
          height: 24px;
          transform: translateX(-60px);
        }
      }
    }
    &__tel {
      @include sp(){
        text-align: center;
        font-size: 12px;
      }
    }
  }
}
.c-contact-01 {
  &__ttl02 {
    display: inline-block;
    font-size: 20px;
    font-weight: 700;
    text-align: center;
    position: relative;
    padding: 0 30px;
    &::before,
    &::after{
      content: "";
      display: block;
      width: 2px;
      height: calc(100% - 10px);
      background-color: #000;
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto 0;
    }
    &::before{
      left: 10px;
      transform: rotate(-30deg);
    }
    &::after{
      right: 10px;
      transform: rotate(30deg);
    }
  }
}
.contact-box02 {
  margin-top: 38px;
  text-align: center;
}
.contact-btn {
  padding: 48px 10px;
  width: 100%;
  display: inline-block;
  border-radius: 8px;
  background: linear-gradient(#3d044d 0%, #6a217e 49.75%, #3d044d 100%);
  font-size: 28px;
  font-weight: bold;
  color: #fff;
}
.contact-btn1 {
