//select
.select-list-wrap{
  margin-top: 48px;
  @include sp{
    margin-top: 38px;
  }
}
.select-list{
  >li+li{
    margin-top: 64px;
    @include sp{
      margin-top: 38px;
    }
  }
  &__item{
    
    background-color: $main_color02;
    padding: 40px;
    border-radius: 20px;
    position: relative;
    @include sp{
      padding: 138px 16px 18px;
    }
  }
  &__point{
    background-color: #FCCE53;
    display: inline-block;
    border-radius: 8px 8px 0 0;
    padding: 35px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 150px;
    position: absolute;
    top: -24px;
    left: 24px;
    @include sp{
      left: 0;
      right: 0;
      margin: 0 auto;
      padding: 16px 0 38px;
      width: 118px;
      top: -14px;
    }
    &::before{
      content: "";
      display: block;
      width: 100%;
      height: 18px;
      border-bottom: 18px solid $main_color02;
      border-right: 75px solid transparent;
      border-left: 75px solid transparent;
      position: absolute;
      left: 0;
      bottom: 0;
      @include sp{
        border-right: 59px solid transparent;
        border-left: 59px solid transparent;
      }
    }
    span.txt{
      font-family: "ShipporiMincho";
      font-size: 32px;
      font-weight: bold;
      color: $main_color01;
      line-height: 1;
      @include sp{
        font-size: 20px;
      }
    }
    span.num{
      font-family: "ShipporiMincho";
      font-size: 80px;
      font-weight: bold;
      color: $main_color01;
      line-height: 1;
      @include sp{
        font-size: 64px;
      }
    }
  }
  &__ttl{
    font-size: 35px;
    font-weight: bold;
    color: #fff;
    @include sp{
      text-align: center;
      font-size: 28px;
    }
  }
  &__box01{
    @include pc{
      padding-left: 170px;

    }
  }
  &__box02{
    margin-top: 24px;
    display: flex;
    gap: 0 40px;
    @include sp{
      gap: 0 13px;
      margin-top: 8px;
    }
  }
  &__box03{
    margin-top: 55px;
    position: relative;
    @include sp{
      margin-top: 36px;
    }
    >img{
      width: 100%;
    }
    >video{
      width: 100%;
    }
  }
  &__box04{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    transform: translateY(-50%);
    display: flex;
    justify-content: center;
    z-index: 2;
  }
  &__box05{
    margin-top: 24px;
    display: flex;
    justify-content: center;
  }
  &__video-box{
    margin-top: 55px;
    position: relative;
    padding-top: 45.5%;
    @include sp{
      margin-top: 36px;
      padding-top: 69.7%;
    }
    >video{
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  &__img-wrap{
    width: calc((100% - 40px)/2);
    @include sp{
      width: calc((100% - 13px)/2);
    }
  }
  &__img{
 
  }
  &__img.pat01{
    padding-top: 0;

  }
  &__dec{
    font-size: 28px;
    font-weight: bold;
    padding: 8px 12px;
    display: inline-block;
    background-color: $main_color01;
    color: #fff;
    text-align: center;
    position: relative;
    @include sp{
      font-size: 16px;
      padding: 8px 4px;
    }
    &::before,
    &::after{
      content: "";
      display: block;
      width: 36px;
      height: 100%;
      border-top: 30px solid $main_color01;
      border-bottom: 30px solid $main_color01;
      border-left: 36px solid transparent;
      position: absolute;
      top: 0;
      @include sp{
        width: 20px;
        border-top: 20px solid $main_color01;
        border-bottom: 20px solid $main_color01;
        border-left: 20px solid transparent;
      }
    }
    &::before{
      right: calc(100% - 1px);
    }
    
    &::after{
      left: calc(100% - 1px);
      transform: rotate(180deg);
      transform-origin: center;
    }
    span.dec{
      content: "";
      display: block;
      position: absolute;
    }
  }

  &__dec02{
    font-size: 28px;
    font-weight: bold;
    padding: 8px 12px;
    display: inline-block;
    background-color: #fff;
    color: $main_color01;
    text-align: center;
    position: relative;
    @include sp{
      font-size: 16px;
      padding: 8px 4px;
    }
    &::before,
    &::after{
      content: "";
      display: block;
      width: 36px;
      height: 100%;
      border-top: 30px solid #fff;
      border-bottom: 30px solid #fff;
      border-left: 36px solid transparent;
      position: absolute;
      top: 0;
      @include sp{
        width: 20px;
        border-top: 20px solid #fff;
        border-bottom: 20px solid #fff;
        border-left: 20px solid transparent;
      }
    }
    &::before{
      right: calc(100% - 1px);
    }
    
    &::after{
      left: calc(100% - 1px);
      transform: rotate(180deg);
      transform-origin: center;
    }
    span.dec{
      content: "";
      display: block;
      position: absolute;
    }
  }
  &__txt{
    text-align: center;
    margin-top: 16px;
    font-weight: bold;
    font-size: 24px;
    font-family: 'ShipporiMincho';
    color: #110239;
    @include sp{
      font-size: 14px;
      margin-top: 8px;
    }
 
  }
  &__caution{
    font-size: 16px;
    color: #fff;
    margin-top: 10px;
    text-align: right;
  }
}
.select-list02{
  margin-top: 6px;
  @include sp{
    margin-top: 16px;
  }
  > li+li{
    margin-top: 8px;
  }
  &__item{
    display: flex;
    align-items: flex-start;

  }
  &__check{
    width: 20px;
    height: 20px;
    background-color: #fff;
    border-radius: 4px;
    margin-right: 8px;
    margin-top: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    @include sp{
      margin-top: 3px;
    }
  }
  &__txt{
    color: #fff;
    font-size: 22px;
    font-weight: bold;
    width: calc(100% - 28px);
    @include sp{
      font-size: 16px;
    }
    >span.caution{
      position: relative;
      &::before{
        content: "※";
        display: inline-block;
        position: absolute;
