.intro-box-wrap {
  // max-height: calc(5em + 20px);
  position: relative;
  @include sp(){
    &__txt {
      max-height: 100px;
      overflow: hidden;
      position: relative;
      transition: $transition01;
      // height: 100%;
    }
    &__txt::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 50%;
      bottom: 0;
      background-image: linear-gradient(rgba(255, 255, 255, 0), #ffffff);
      pointer-events: none;
      transition: 1s;
    }
    input {
      visibility: hidden;
    }
    label {
      position: absolute;
      right: 0px;
      bottom: 0px;
      display: block;
      font-size: .9em;
      padding: .20em 5px;
      background: #aaa;
      box-shadow:-5px 0 white;
      color: white;
      z-index:999;
      cursor: pointer;
      text-transform: uppercase;
    }
    label:before {
      content: "全文表示";
    }
    input:checked + label:before {
      content: "閉じる";
    }
    input[type=checkbox]:checked ~ .intro-box-wrap__txt {
      max-height: 1000px;
    }
    input[type=checkbox]:checked ~ .intro-box-wrap__txt::before {
      opacity: 0;
    }
  }
}
