@use '../mixin/mixin' as *;
@use '../setting/setting' as *;

/* --------------------------------
c-header
----------------------------------- */

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
}
#header {
  background: #fff;
  @include sp(){
    padding: 13px 0;
  }
}
.header {
  &__box {
    background: #fff;
    display: flex;
    justify-content: space-between;
    @include pc(){
      height: 99px;
    }
    @include sp {
      display: block;
    }
  }
  &__box01 {
    display: flex;
    align-items: center;
    column-gap: 2px;
    height: 100%;
    @include sp(){
      align-items: flex-start;
    }
  }
  &__img {
    display: flex;
    align-items: center;
    @include pc(){
      width: 271px;
      height: 64px;
    }
    @include sp(){
      width: 161px;
    }
  }
  &__box02 {
    position: relative;
    z-index: 1;
  }
  &-sp__box {
    width: 100%;
    display: flex;
    position: fixed;
    bottom: 0;
    .header__btn {
      &:first-child {
        @include sp(){
          width: 25.6%;
        }
      }
      &:nth-child(3) {
        @include sp(){
          width: 100% - 51.2%;
        }
      }
    }
    .header__box02 {
      @include sp(){
        width: 25.6%;
      }
    }
  }
}
.contact-btn02 {
  display: block;
  padding: 44px 25px 8px;
  background: $main_color03;
  color: #fff;
  font-size: 14px;
  position: relative;
  text-align: center;
  font-weight: bold;
  @include sp(){
    padding-left: 0;
    padding-right: 0;
  }
  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    margin: auto;
    transform: translateY(-20px);
    width: 32px;
    height: 32px;
    background: url(../img/header-mail.svg) no-repeat;
    background-position: center top;
  }
}
.contact-btn03 {
  display: block;
  padding: 44px 25px 8px;
  background: #04C755;
  color: #fff;
  font-size: 14px;
  position: relative;
  text-align: center;
  font-weight: bold;
  @include sp(){
    padding-left: 0;
    padding-right: 0;
  }
  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    margin: auto;
    transform: translateY(-23px);
    width: 28px;
    height: 28px;
    background: url(../img/header-line.svg) no-repeat;
  }
}
.contact-btn04 {
  display: block;
  padding: 14px 25px 4px;
  background: #4694D1;
  color: #fff;
  position: relative;
  font-size: 24px;
  font-family: 'ShipporiMincho';
  font-weight: 700;
  text-align: center;
  @include sp(){
    padding: 5px 10px;
    height: 100%;
    font-size: 23px;
  }
  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
  }
  .small {
    font-size: 12px;
    font-weight: 500;
  }
}
.btn-dec {
  padding: 1px 8px;
  background: #110239;
  border-radius: 9px;
  position: absolute;
  top: -10px;
  left: 0;
  right: 0;
  font-size: 12px;
  color: #fff;
  margin: auto;
  z-index: 2;
  white-space: nowrap;
}