@use '../mixin/mixin' as *;
@use '../setting/setting' as *;

.c-box01{
  margin-top: 160px;
  @include sp{
    margin-top: 120px;
  }
}

.c-box02{
  margin-top: 150px;
  @include sp{
    margin-top: 80px;
  }
}

.c-box03{
  margin-top: 120px;
  @include sp{
    margin-top: 40px;
  }
}

.c-box04{
  margin-top: 200px;
  @include sp{
    margin-top: 120px;
  }
}

.c-box05 {
  margin-top: 96px;
}


.c-ttl{
  font-size: 56px;
  font-weight: bold;
  line-height: 1.44;
  padding-bottom: 72px;
  color: $main_color01;
  position: relative;
  text-align: center;
  @include sp{
    font-size: 32px;
    padding-bottom: 64px;
  }
  &::before{
    content: "";
    display: block;
    width: 3px;
    height: 48px;
    background-color: $main_color01;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
  }
  &.pat01{
    padding-bottom: 40px;
    &::before{
      content: none;
    }
  }
  &.ptn02 {
    @include sp(){
      font-size: vw(20,375);
    }
  }
  >span.color01{
    color: $main_color02;
  }
  .parts01{
    font-size: 100px;
    position: relative;
    margin-left: 0.05em;
    margin-right: 0.05em;
    @include sp(){
      font-size: 56px;
    }
    &:before{
      content: '※';
      display: inline-block;
      font-size: 32px;
      position: absolute;
      top: 0;
      left: 100%;
      @include sp(){
        font-size: 16px;
      }
    }
  }
}
.c-table-wrap{
  margin-top: 24px;
}
.c-table{
  width: 100%;
  border-radius: 4px;
  overflow: hidden;
  tr{
    border-bottom: 4px solid #fff;
  }
  th{
    color: #fff;
    font-weight: bold;
    font-size: 24px;
    background-color: $main_color02;
    padding: 21px;
    text-align: center;
    @include pc{
      white-space: nowrap;
    }
    @include sp{
      font-size: 18px;
      padding: 24px 6px;
    }
  }
  td{
    font-size: 20px;
    padding: 24px;
    background-color: rgba(158, 154, 203,0.08);
    @include sp{
      font-size: 16px;
      padding: 16px 8px;
    }
  }
}
.c-list-wrap{
  margin-top: 80px;
}
.c-list{
  &__item{
    @include pc{
      display: flex;
      align-items: center;
    }
  }
  &__box01{
    position: relative;
    @include sp{
      padding: 0 26px;
    }
  }
  &__step{
    position: absolute;
    top: 0;
    left: 0;
    width: 110px;
    height: 110px;
    background-color: $main_color02;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-weight: bold;
    font-size: 28px;
    @include pc{
      transform: translate(-50% ,-50%);
    }
    @include sp{
      transform: translateY(-50%);
    }
  }
  &__img{
    border-radius: 8px;
    border: solid 3px $main_color02;
    @include pc{
      width: 284px;
      height: 291px;
    }
    >img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__box02{
    @include pc{
      width: calc(100% - 284px);
      padding:0 0 0 24px;
    }
    @include sp{
      margin-top: 12px;
    }
  }
  &__txt{
    border-radius: 16px;
    background-color: $main_color02;
    padding: 24px;
  }
  &__ttl{
    font-weight: bold;
    font-size: 32px;
    color: #fff;
    padding-bottom: 14px;
    border-bottom: 3px solid #fff;
    @include sp{
      font-size: 28px;
    }
  }
  &__cap{
    padding-top: 16px;
    font-size: 20px;
    color: #fff;
    @include sp{
      font-size: 16px;
    }
    >a.link{
      color: #fff;
    }
  }
  &__arrow{
    padding: 24px 134px;
    @include sp{
      padding: 24px 0 79px;
      text-align: center;
    }
    >img{
      width: 21px;
      height: 92px;
    }
    &.pat-right{
      @include pc{
        display: flex;
        justify-content: flex-end;
      }
    }
  }
}

//偶数
.c-list > li:nth-child(even){
  .c-list{
    &__box01{
      order: 2;
    }
    &__box02{
      order: 1;
      @include pc{
        padding: 0 24px 0 0;
      }
    }
    &__step{
      @include pc{
        left: initial;
        right: 0;
        transform: translate(50%,-50%);
      }
    }
  } 
}

.c-txt {
  font-size: 48px;
  font-weight: 700;
  color: $main_color02;
  @include sp(){
    font-size: 28px;
  }
  .black {
    font-size: 40px;
    color: #000;
    @include sp() {
      font-size: 20px;
    }
  }
  .small {
    font-size: 40px;
    @include sp(){
      font-size: 20px;
    }
  }
}


.c-list01 {
  &__ttl {
    text-align: center;
  }
  &__item {
    margin-top: 80px;
    &:first-child {
      margin-top: 24px;
    }
  }
  &__box {
    margin-top: 32px;
    display: flex;
    position: relative;
    @include sp(){
      display: block;
    }
  }
  &__video {
    width: 405px;
    height: 705px;
    border: #000 solid 8px;
    border-radius: 24px;
    position: relative;
    @include sp(){
      margin: 0 auto;
      width: 333px;
      height: 580px;
    }
    &::before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      margin: auto;
      background: #000;
      width: 184px;
      height: 44px;
      border-radius: 0 0 16px 16px;
      z-index: 2;
    }
  }
  &__video01 {
    position: relative;
    height: 100%;
    video {
      border-radius: 16px;
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
      top: 0;
      left: 0;
    }
  }
  &__box02 {
    width: calc(100% - 405px);
    @include sp(){
      width: 100%;
    }
  }
  &__nam {
    padding-top: 97px;
    font-size: 140px;
    font-family: 'ShipporiMincho';
    color: $main_color03;
    opacity: 0.2;
    line-height: 1.4;
    @include sp(){
      padding-top: 0;
      font-size: 88px;
    }
  }
  &__txt {
    @include pc(){
      transform: translateY(-60px);
    }
  }
  &__box03 {
    margin-left: -60px;
    padding: 24px;
    border: solid 3px $main_color02;
    background: #fff;
    @include sp(){
      transform: translateY(-32px);
      margin-left: 0;
    }
  }
  &__txt01 {
    padding-bottom: 24px;
    border-bottom: solid 2px $main_color02;
    font-size: 27px;
    font-weight: bold;
    color: $main_color02;
    text-align: center;
    @include sp(){
      text-align: left;
    }
  }
  &__txt02 {
    margin-top: 24px;
  }
  &__txt03 {}
}


.c-table02 {
  table {
    border-collapse: separate;
    border-spacing: 8px;
  }
  tbody {
    width: 510px;
    height: 100%;
  }
  th,
  td {
    @include sp(){
      display: block;
    }
    
  }
  tr {
    @include sp(){
      text-align: left;
    }
  }
  th {
    padding: 4px 10px;
    background: #000;
    color: #fff;
    width: 82px;
    text-align: center;
    border-radius: 2px;
    @include pc{
      word-break: keep-all;
    }
    @include sp(){
      font-size: 12px;
      display: block;
      min-width: 82px;
    }
  }
  td {
    @include sp(){
      width: 100%;
      font-size: 12px;
    }
  }
}


.c-list02-wrap{
  margin-top: 48px;
}
.c-list02{
  >li+li{
    margin-top: 72px;
    @include sp{
      margin-top: 57px;
    }
  }
  &__box01{
    padding: 32px 72px 16px;
    background-color: $main_color02;
    color: #fff;
    position: relative;
    border-radius: 8px 8px 8px 8px;
    transition: $transition01;
    @include sp{
      padding: 43px 15px 15px 24px;
    }
    &.is-active{
      border-radius: 8px 8px 0 0;
    }
  }
  &__dec{
    position: absolute;
    left: 45px;
    top: 0;
    width: 208px;
    transform: translateY(#{calc(-50% + 5px)});
    @include sp{
      left: 0;
      right: 0;
      margin: 0 auto;
    }
  }

  &__toggle{
    display: block;
    width: 64px;
    height: 64px;
    border-radius: 50%;
    background-color: #fff;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 40px;
    margin: auto 0;
    @include sp{
      width: 32px;
      height: 32px;
      right: 24px;
    }
    &::before,
    &::after{
      content: "";
      display: block;
      width: 48px;
      height: 8px;
      background-color: $main_color02;
      border-radius: 4px;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      margin: auto;
      @include sp{
        width: 22px;
        height: 3px;
      }
    }
    &::after{
      transform: rotate(90deg);
      transition: $transition01;
    }
    &.is-active{
      &::after{
        transform: rotate(0);
      }
    }
  }
  
  &__ttl01{
    font-weight: bold;
    font-size: 28px;
    line-height: 1;
    @include sp{
      font-size: 20px;
    }
    &.ptn01 {
      letter-spacing: -0.18em;
    }
  }
  &__price{
    font-weight: bold;
    font-size: 48px;
    font-family: "ShipporiMincho";
    display: flex;
    align-items: flex-end;
    line-height: 1;
    @include sp{
      font-size: 32px;
      margin-top: 5px;
      padding-left: 12px;
    }
    span.box{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    span.tax{
      font-size: 12px;
      font-weight: bold;
      line-height: 1;
      padding-left: 3px;
      @include sp{
        font-size: 10px;
      }
    }
    span.yen{
      line-height: 1;
      font-size: 28px;
      font-weight: bold;
    }
  }
  &__box02{
    padding: 40px;
    border: solid 4px $main_color02;
    border-radius: 0 0 8px 8px;
    display: none;
    @include sp{
      padding: 40px 14px;
    }
  }
  &__box03{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &__box04{
    @include pc{  
      display: flex;
      align-items: flex-end;
    }
  }
  &__img{
    @include pc{
      width: 390px;
    }
    @include sp{
      order: 2;
      margin-top: 16px;
    }
  
  }
  &__txt01{
    @include pc{
      width: calc(100% - 390px);
      padding-left: 24px;
    }
    @include sp{
      order: 1;
    }
  }
  &__ttl02{
    font-size: 30px;
    font-weight: bold;
    color: $main_color02;
    @include sp{
      font-size: 23px;
    }
  }
  &__cap01{
    font-size: 22px;
    font-weight: bold;
    margin-top: 15px;
    @include sp{
      font-size: 17px;
    }
  }
}

.c-list03-wrap{
  margin-top: 24px;
}
.c-list03{
  margin-top: -11px;
  @include pc{
    margin-left: -40px;
    
  display: flex;
  flex-wrap: wrap;
  }
  >li{
    margin-top: 11px;
    @include pc{
    margin-left: 40px;

    }
  }
  &__item{
    display: flex;
    align-items: center;
    @include sp{
      justify-content: center;
    }
  }
  &__type{
    color: #fff;
    font-weight: bold;
    font-size: 20px;
    padding: 8px 22px;
    min-width: 184px;
    border-radius: 30px;
    background-color: $main_color02;
    text-align: center;
    @include sp{
      font-size: 14px;
      min-width: 148px;
      padding: 8px;

    }
  } 
  &__star{
    font-weight: bold;
    font-size: 24px;
    color: $main_color02;
    margin-left: 16px;
    @include sp{
      font-size: 20px;
    }
  } 

}
.c-list04-wrap{
  margin-top: 32px;
  @include sp{
    margin-top: 24px;
  }
}
.c-list04{
  display: flex;
  flex-wrap: wrap;
  gap: 0 40px;
  @include sp{
    gap: 0 12px;
  }
  >li{
    width: calc((100% - 40px)/2);
    @include sp{
      width: calc((100% - 12px)/2);
    }
  }
  &__item{}
  &__box{
    position: relative;
  }
  &__dec{
    position: absolute;
    top: 5px;
    left: -8px;
    background-color: $main_color02;
    border-radius: 0 7px 7px 0;
    color: #fff;
    width: 140px;
    padding: 10px;
    text-align: center;
    font-size: 16px;
    font-weight: bold;
    font-family: "ShipporiMincho";
    z-index: 2;
    @include sp{
      font-size: 10px;
      width: auto;
      display: inline-block;
      padding: 2px 16px;
      top: 0;
    }
    &::before{
      content: "";
      display: block;
      width: 8px;
      height: 6px;
      border-top:3px solid $main_color01 ;
      border-right:4px solid $main_color01 ;
      border-bottom:3px solid transparent ;
      border-left:4px solid transparent ;
      position: absolute;
      left: 0;
      top: 100%;

    }
  }
  &__img{
    position: relative;
    >img,
    >video{
      width: 100%;
    }
  }

}
.c-list05-wrap{
  margin-top: 24px;
}
.c-list05{
  >li{
    border-radius: 24px;
    @include pc{
      padding-left: 190px;
    }
  }
  >li:nth-child(even){
    padding-left: 0;
    @include pc{
      padding-right: 190px;
    }
    .c-list05__box01{
      order: 2;
      border-radius: 0 24px 24px 0;
      @include pc{
        padding: 53px 40px 53px 24px
      }
      @include sp{
        border-radius: 0 0 8px 8px;
      }
    }
    .c-list05__img{
      order: 1;
      border-radius: 24px 0  0 24px;
      @include pc{
        padding: 0 0 0 24px;
      }
      @include sp{
        border-radius:8px 8px 0 0;
      }
    }
  }
  >li+li{
    margin-top: 32px;
  }
  &__item{
    display: flex;
    flex-wrap: wrap;
    
  }
  &__box01{
    padding: 53px 24px 53px 40px;
    background-color: $main_color02;
    border-radius: 24px 0 0 24px;
    @include pc{
      // width: calc(100% - 33.2%);
      width: 66.8%;
    }
    @include sp{
      order: 2;
      border-radius:0 0 8px 8px;
      padding: 16px;
    }
  }
  &__box02{
    background-color: #fff;
    border-radius: 24px;
    padding: 24px 48px;
    @include sp{
      padding: 24px 8px;
    }
  }
  &__img{
    border-radius:0 24px 24px 0;
    overflow: hidden;
    @include pc{
      width: 33.2%;
      padding-right: 24px;
      background-color: $main_color02;
    }
    @include sp{
      order: 1;
      border-radius:8px 8px 0 0;
    }
    >img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  &__ttl{
    @include pc{
      border-bottom: 1px solid #000;
    }
    @include sp{
      text-align: center;
      position: relative;
    }
    &::before{
      @include sp{
        content: "";
        display: block;
        width: calc(100% - 80px );
        height: 1px;
        background-color: #000;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        margin: 0 auto;
      }
    }
    >span.num{
      font-weight: bold;
      font-size: 48px;
      color: $main_color01;
      padding-right: 4px;
    }
    >span.txt{
      color: $main_color02;
      font-size: 32px;
      font-weight: bold;
    }

  }
  &__cap{
    margin-top: 8px;
    font-size: 20px;
    @include sp{
      font-size: 16px;
      margin-top: 16px;
    }
  }
  &__tag{
    margin-top: 16px;
    display: flex;
    flex-wrap: wrap;
    span.tag{
      display: inline-block;
      padding: 8px 16px;
      border-radius: 23px;
      background-color: $main_color02;
      color: #fff;
      font-weight: 500;
      font-size: 18px;
      @include sp{
        display: block;
        width: 100%;
        text-align: center;
      }
    }
    span+span{
      @include pc{
        margin-left: 24px;
      }
      @include sp{
        margin-top: 8px;
      }
    }
  }

}
