/*------------------------------------------------------------------------------
  reset SP
------------------------------------------------------------------------------*/
@media only screen and (max-width: 640px) {
  body {
    font-size: 14px;
    position: relative;
    -webkit-appearance: none;
    -webkit-text-size-adjust: 100%;
  }

  .outer-block {
    min-width: 320px;
  }

  .inner-block {
    padding: 0 20px;
    width: auto;
  }

  #wrapper {
    min-width: 320px;
    position: relative;
    overflow: hidden;
  }

  input[type=submit] {
    -webkit-text-size-adjust: 100%;
  }

  input,
select,
textarea {
    font-size: 16px !important;
  }

  .pc {
    display: none !important;
  }
}
